


















class SumAmountModel {
    FirstAmount: number;
    SecondAmount: number;

    get Total(): number {
        return this.FirstAmount + this.SecondAmount
    }

    constructor() {
        this.FirstAmount = 0;
        this.SecondAmount = 0;
    }
}

let sumAmount = new SumAmountModel();

export default {
    data() {
        return sumAmount;
    },
    computed: {
        Total: function () {
            return sumAmount.Total
        }
    }
}    




